import React from "react"
import { Link } from "gatsby"

import '../../styles/layout.scss'
import '../../styles/header.scss'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faInstagram,faTwitter,faFacebookF } from '@fortawesome/free-brands-svg-icons'

const Header = ({ title,props,location }) => {
  let header
  let nav
  let style
  const path = "/"//globalHistory.location.pathname.replace("-japanese","")

  nav = (
    <div id="globalNav" class="header-logo-menu">
      <div id="nav-drawer">
        <input id="nav-input" type="checkbox" class="nav-unshown" />
        <label id="nav-open" for="nav-input" htmlFor="nav-input"><span></span></label>
        <label class="nav-unshown" id="nav-close" for="nav-input" htmlFor="nav-input"></label>
        <div id="nav-content">
        <nav>
          <ul>
            <li><Link to="/jp/">Top</Link></li>
            <li><Link to="/jp/projects/">Project</Link></li>
            <li><Link to="/jp/news/">News</Link></li>
            <li><Link to={path}>In English</Link></li>
            <li>
              <a href="https://www.instagram.com/otq.studio/"><FontAwesomeIcon icon={faInstagram} aria-label="Instagram Brand Icon" /></a>
              <a href="https://www.twitter.com/otqstudio/"><FontAwesomeIcon icon={faTwitter} aria-label="Twitter Brand Icon" /></a>
              <a href="https://facebook.com/OTQStudio"><FontAwesomeIcon icon={faFacebookF} aria-label="Facebook Brand Icon" /></a></li>
          </ul>
        </nav>
        </div>
      </div>
    </div>
  )

  style={
    fontWeight: 900,
    fontSize: '2.0rem',
    color: "#000",
  }

  header = (
    <nav>
      <Link style={style} to={`/jp`}>OTQ</Link>
    </nav>
  )

  return (
    <header>
      <div className="flex">
        {header}
        {nav}
      </div>
    </header>
  )
}

export default Header
